import { useSelector, useDispatch } from "react-redux";
const AllUsers = () => {
  const allusers = useSelector((state) => state.users.val);
  return (
    <>
      {allusers.map((users) => {
        return (
          <div>
            {users.name}
            {users.username}
          </div>
        );
      })}
    </>
  );
};
export default AllUsers;
